import React, {FunctionComponent} from 'react';
import getComponent from "../../../../utils/cms/component-resolver";

import ClassNames from './BlankPage.module.scss';
import PageClassNames from '../../../../styles/page.module.scss';
import Navigation from "../../../Navigation/Navigation";
import {CmsPageProps} from "../../../../types/CmsPageProps";
import AppLink from "../../../../util-components/Link/Link";

interface Props {
    pageContext: {
        fields: {
            items: any[]
        }
    }
}

const BlankPage: FunctionComponent<Props & CmsPageProps> =
    ({pageContext: {fields, title}}) => {
        const components = fields.items.map(item => {
            const Comp = getComponent(item);
            return Comp;
        });

        return (
            <div className={PageClassNames.Page}>
                <div className={ClassNames.BlankPage}>
                    <Navigation/>
                    <div><AppLink to="/">Home (hard for now)</AppLink></div>
                    {components}
                </div>
            </div>
        );
    };

export default BlankPage;
